.App {
  font-size: 20px;
}

.InitialContainer {
  height: 100vh;
  width: 80vw;
  display: flex;
  align-items: center;
  margin: 0 auto;
}


.AboutInfoContainer {
  padding-top: 6rem;
  display: flex;
  flex-direction: column;
  overflow-x: auto;
  margin: 0 auto;
  max-width: 50%;
}

.MainPicture {
  max-width: min(40%, 600px);
  margin: 0 auto;
  height: auto;
}

.Links {
  list-style-type: none;
  display: flex;
  gap: 30px;
  padding: 0px;
  align-self: center;
}

.InfoContainer {
  width: min(100vw, 1200px);
  margin: 0 auto;
}

h2 {
  margin-bottom: 0;
}

@media screen and (max-width: 800px) {
  .InitialContainer {
    flex-direction: column;
  }

  .MainPicture {
    width: 80vw;
  }

}

